export const RequireRule = {
  rule: /.+/,
  match: true,
  message: '  .',
};

export const CantContainWhitespace = {
  rule: /\s/,
  match: false,
  message: '   .',
};

export const CantStartNumber = {
  rule: /^\d/,
  match: false,
  message: '     .',
}

export const MinimumLengthLimit = limit => ({
  rule: new RegExp(`(.){${limit}}`),
  match: true,
  message: ` ${limit}   .`,
});
